






















import Vue from 'vue';
import { createURLParams } from '@/lib/util';
import goals from '@/lib/fathomGoals';
import { Project } from '@/configTypes';

/**
 * ProjectCard is a small card used to render a Project. See [[Project]] for more details.
 */
export default Vue.extend({
  name: 'ProjectCard',
  props: {
    /**
     * Name of section this card was rendered in
     */
    section: { type: String, required: true },
    /**
     * Project to render
     */
    project: {
      type: Object as () => Project,
      required: true,
    },
  },
  data: () => ({ isActive: false }),
  computed: {
    id(): string { return `card-${this.project.name.toLowerCase().replace(' ', '-')}`; },
  },
  methods: {
    openModal() {
      // track that button was clicked
      this.$fathom.trackGoal(goals.PROJECTCARD_CLICK);

      // track the view of a project card as if it were a page
      this.$fathom.trackPageview({
        // 'fake' encode URL parameters as normal path components, since fathom discards URL queries
        url: `/${createURLParams({ project: this.project.name })}`,
      });

      this.$emit('projectClicked', { isActive: true, activeProjectName: this.project.name });
    },
  },
});
